var that;
import uniNumberBox from '@/components/uni-number-box/uni-number-box.vue';

function getDate(type) {
	const date = new Date();
	let year = date.getFullYear();
	let month = date.getMonth() + 1;
	let day = date.getDate();
	if (type === 'start') {
		// year = year - 60;
	} else if (type === 'end') {
		year = year + 2;
	}
	month = month > 9 ? month : '0' + month;
	day = day > 9 ? day : '0' + day;
	return `${year}-${month}-${day}`;
}
export default {
	components: {
		uniNumberBox
	},
	data() {
		return {
			orderidnumber: -1,
			raffleJournalId: 0,
			note: '', //备注
			numberValue: 1,
			name: "",
			price: 0.1,
			prizeQuantity: 200,
			url: "https://zzwl1zsxn.oss-cn-hangzhou.aliyuncs.com/images/2019/05/31/cccd2c3fab3eaa1071208a5ebae1190f.png",
			peopleQuantity: 1000,
			detail: "<p><img src=\"https://zzwl1zsxn.oss-cn-hangzhou.aliyuncs.com/product/b934449e-27f6-4b12-9ab5-a66615c8555f.jpg\"><img src=\"https://zzwl1zsxn.oss-cn-hangzhou.aliyuncs.com/product/f9907dad-c18f-476e-bac9-c07a859ad7d7.jpg\"></p>",
			raffleJournalStatus: 1,
			isPrize: null,
			nowPeopleCount: 594,
			originalPrice: 90,
			commodityId: 46,
			jindu: 50,
			addressData: {
				name: '添加地址',
				mobile: null
			}
		};
	},
	async onLoad(option) {
		//商品数据
		that = this;
		that.raffleJournalId = option.id;
		that.loadData();
	},
	methods: {
		change(value) {
			console.log(value);
			that.numberValue = 1;
		},
		async loadData() {
			uni.request({
				url: that.$api_url + '/raffle/raffle_journal_detail_user',
				method: 'POST',
				header: {
					'Content-Type': 'application/x-www-form-urlencoded',
					'authorization': uni.getStorageSync('authorization') //自定义请求头信息
				},
				data: {
					'raffleJournalId': that.raffleJournalId
				},

				success: function(res) {
					console.log(res);
					if (res.data.code != 1000) {
						return false;
					}
					that.jindu = Math.floor(res.data.content.nowPeopleCount * 100 / res.data.content.peopleQuantity)
					that.name = res.data.content.name
					that.price = res.data.content.price
					that.prizeQuantity = res.data.content.prizeQuantity
					that.url = res.data.content.url
					that.peopleQuantity = res.data.content.peopleQuantity
					that.detail = res.data.content.detail
					that.raffleJournalStatus = res.data.content.raffleJournalStatus
					that.isPrize = res.data.content.isPrize
					that.nowPeopleCount = res.data.content.peopleQuantity - res.data.content.nowPeopleCount
					that.originalPrice = res.data.content.originalPrice
					that.commodityId = res.data.content.commodityId

				}
			});
		},
		selAddress: function() {
			var that = this;
			uni.chooseAddress({
				success(res) {
					that.addressData = res;
					console.log(res.userName)
					console.log(res.postalCode)
					console.log(res.provinceName)
					console.log(res.cityName)
					console.log(res.countyName)
					console.log(res.detailInfo)
					console.log(res.nationalCode)
					console.log(res.telNumber)
				}
			})
		},

		submit() {
			let that = this;
			if (that.addressData.telNumber == null) {
				uni.showToast({
					title: '请选择地址'
				})

			} else {
				if (-1 != that.orderidnumber) {
					uni.request({
						url: this.$api_url + '/weixin/payment.do', //仅为示例，并非真实接口地址。
						method: 'POST',
						header: {
							'Content-Type': 'application/x-www-form-urlencoded',
							'authorization': uni.getStorageSync('authorization') //自定义请求头信息
						},
						data: {
							"orderId": res.data.content.id
						},
						success: res => {
							console.log('success:' + JSON.stringify(res.data));
							console.log('success:' + res.data.nonceStr);
							uni.requestPayment({
								provider: 'wxpay',
								timeStamp: res.data.timeStamp,
								nonceStr: res.data.nonceStr,
								package: res.data.package,
								signType: 'MD5',
								paySign: res.data.paySign,
								success: function(res) {
									console.log('success:' + JSON.stringify(res));
									uni.navigateBack({
										delta: 2
									})
								},
								fail: function(err) {
									console.log('fail:' + JSON.stringify(err));
								}
							});

						}
					});
				} else {
					uni.request({
						url: that.$api_url + '/order/create', //仅为示例，并非真实接口地址。
						method: 'POST',
						header: {
							'authorization': uni.getStorageSync('authorization') //自定义请求头信息
						},
						data: {
							customer_id: uni.getStorageSync('openid'),
							type: 4,
							commodityItemVO: { //商品规格
								commodityId: that.commodityId,
								quantity: that.numberValue
							},
							raffleJournalId: that.raffleJournalId,
							otherInfo: that.note,
							consignee: that.addressData.userName,
							phone: that.addressData.telNumber,
							address: that.addressData.provinceName + that.addressData.cityName + that.addressData.countyName + that.addressData
								.detailInfo,
						},
						success: res => {

							if (1000 == res.data.code) {
								// uni.redirectTo({
								// 	url: '/pages/money/pay?id=' + res.data.data
								// });
								that.orderidnumber = res.data.content.id
								uni.request({
									url: this.$api_url + '/weixin/payment.do', //仅为示例，并非真实接口地址。
									method: 'POST',
									header: {
										'Content-Type': 'application/x-www-form-urlencoded',
										'authorization': uni.getStorageSync('authorization') //自定义请求头信息
									},
									data: {
										"orderId": res.data.content.id
									},
									success: res => {

										// nonceStr: "8a28b8de713a575801713a575a670001"
										// package: "prepay_id=wx02180215454541b895e6f4921141301600"
										// paySign: "CC92782F19A99A22194EA3E636C44AAA"
										// timeStamp: "1585821735"
										// 仅作为示例，非真实参数信息。
										console.log('success:' + JSON.stringify(res.data));
										console.log('success:' + res.data.nonceStr);
										uni.requestPayment({
											provider: 'wxpay',
											timeStamp: res.data.timeStamp,
											nonceStr: res.data.nonceStr,
											package: res.data.package,
											signType: 'MD5',
											paySign: res.data.paySign,
											success: function(res) {
												console.log('success:' + JSON.stringify(res));
												uni.navigateBack({
													delta: 2
												})
											},
											fail: function(err) {
												console.log('fail:' + JSON.stringify(err));
											}
										});

									}
								});

							} else {
								uni.showModal({
								    title: '提示',
								    content: ''+res.data.message,
									showCancel:false
								});
								
							}

						}
					});
				}
			}
		}
	}
};
